@import '../../styles/variables';

.btn-login {
  cursor:pointer;
  background-color: #fff;
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
  color: $purple;
  margin-right: 50px;
}

.user {
  margin-right: 50px;
}


@media screen and (max-width: 500px) {
  .btn-login, .user{
    margin: 0 0 15px 0;
  }
}