@import '../../styles/variables';

.main__wrapper {
  .years {
    display: flex;
    flex: 1;
    flex-wrap: wrap;
    //justify-content: space-between;
    max-width: 300px;

    .year {
      background-color: $purple;
      color: white;
      padding: 5px 10px;
      border: none;
      border-radius: 5px;
      outline: none;
      cursor: pointer;
      margin: 7px 10px 7px 0;

      &:active{
        box-shadow: inset 0 0 7px #000000;
      }

      &.year-active {
        background-color: darken( $purple, 10% );
      }
    }
  }

  .info {
    margin: 5px 0 20px 0;
  }

  .photos {
    width: 100%;
    display: grid;
    align-content: center;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    grid-auto-rows: 350px;
    grid-gap: 8px;

    .error{
      color: red;
      margin: 0;
    }

    .load{
      margin: 0;
    }

    .photo {
      position: relative;

      .photo__img {
        width: 100%;
        height: 100%;
        //min-height: 350px;
        object-fit: cover;
      }

      .photo__content {
        position: absolute;
        right: 0;
        bottom: 0;
        padding: 7px;
        background: rgba(0, 0, 0, .6);
        margin: 0;
        color: white;
      }
    }
  }
}