@import '../../styles/variables';

.app {
  .app__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: $purple;
    color: white;

    .app__title {
      margin-left: 50px;
    }
  }

  .app__main {
    padding: 30px;
  }
}

@media screen and (max-width: 500px) {
  .app {
    .app__header{
      flex-direction: column;
      .app__title {
        margin: 20px 0;
      }
    }
  }
}